* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
}

button {
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}

a {
  color: inherit;
  text-decoration: none;
}

@font-face {
  font-family: 'geomanist';
  src: url(/jeremymorrison.ca/static/media/geomanist-regular-webfont.a35649b1.woff2) format('woff2'),
    url(/jeremymorrison.ca/static/media/geomanist-regular-webfont.deb2e275.woff) format('woff');
}

@font-face {
  font-family: 'adam';
  src: url("/jeremymorrison.ca/static/media/ADAM.CG PRO.c455ec93.otf") format('opentype');
}

html {
  /* best practice: percentage allows user to chnage base size in browser and have control over font sizing */
  /* 62.5% sets 1rem = 10px if font size is 16px. */
  font-size: 62.5%;
}

body {
  /* 1.6rem = 16px if browser font size is 16 */
  font-size: 1.6rem;
  font-family: 'Open Sans', sans-serif;
  font-family: var(--font-content);
  background-color: #d3cfd2;
  background-color: var(--color-light);
  height: 100%;
  color: white;
}

:root {
  --color-primary-dark: #1e3348;
  --color-primary-medium: #2a4765;
  --color-primary-light: #365c81;
  --color-secondary: #66b0b7;
  --color-tertiary: #353135;
  --color-light: #d3cfd2;

  --font-display-primary: 'Montserrat', sans-serif;
  --font-display-secondary: 'Oswald', sans-serif;
  --font-display-3: 'Coda', cursive;
  --font-content: 'Open Sans', sans-serif;
  --width-sidebar: 200px;
}

