@font-face {
  font-family: 'geomanist';
  src: url(../fonts/geomanist-regular-webfont.woff2) format('woff2'),
    url(../fonts/geomanist-regular-webfont.woff) format('woff');
}

@font-face {
  font-family: 'adam';
  src: url('../fonts/ADAM.CG PRO.otf') format('opentype');
}

html {
  /* best practice: percentage allows user to chnage base size in browser and have control over font sizing */
  /* 62.5% sets 1rem = 10px if font size is 16px. */
  font-size: 62.5%;
}

body {
  /* 1.6rem = 16px if browser font size is 16 */
  font-size: 1.6rem;
  font-family: var(--font-content);
  background-color: var(--color-light);
  height: 100%;
  color: white;
}

:root {
  --color-primary-dark: #1e3348;
  --color-primary-medium: #2a4765;
  --color-primary-light: #365c81;
  --color-secondary: #66b0b7;
  --color-tertiary: #353135;
  --color-light: #d3cfd2;

  --font-display-primary: 'Montserrat', sans-serif;
  --font-display-secondary: 'Oswald', sans-serif;
  --font-display-3: 'Coda', cursive;
  --font-content: 'Open Sans', sans-serif;
  --width-sidebar: 200px;
}
